<!-- 【分类列表】页面 -->
<template>
  <div class="front-page">
    <home-header></home-header>
    <div class="front-body">
      <div >
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ path: '/front' }">首页</el-breadcrumb-item>
              <el-breadcrumb-item>分类列表</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <sui-card v-if="dataList.length>0" :dataList=dataList pageName="categoryDetails"></sui-card>
          <el-empty v-else class="sui-empty" description="暂无数据"></el-empty>
        </el-card>
        <div class="pagination" style="text-align: center">
          <el-pagination
            @current-change="handleCurrentChange"
            :page-size="queryForm.size"
            layout="total, prev, pager, next"
            :total="total">
          </el-pagination>
        </div>
      </div>

    </div>
    <home-footer></home-footer>
  </div>

</template>

<script>
    export default {
      data() {
        return {
          total: 0, //记录数
          dataList: [],
          queryForm: {
            current: 1,
            size: 8,
            id: "",
            picture: "",
            title: "",
            categoryId: "",
            publishTime: "",
            content: ""
          }
        };
      },
      mounted() {
        debugger
        let params = this.$route.query;
        this.queryForm.categoryId = params.id;
        //初始化数据
        this.getList();
      },
      methods: {
        //点击搜索
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //查询列表数据
        getList() {
          this.$request.doGet('/api/notice/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //选择页码
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
      }
    };
</script>
<style scoped>
  .box-card {
    min-height: 800px
  }
</style>



