<!-- 【前台首页】 -->
<template>
  <div class="front-page">
    <!--首页头部区域：包含登录|注册-->
    <home-header></home-header>
    <!--首页查询区域：包含查询按钮-->
    <home-search @search="search"></home-search>
    <!--首页中间区域：包含列表展示-->
    <home-body ref="homeBody"></home-body>
    <!--首页底部区域：包含底部说明-->
    <home-footer></home-footer>
  </div>
</template>

<style scoped>

</style>

<script>
  export default {
    name: "home",
    methods: {
      search(queryForm){
        this.$refs.homeBody.getList(queryForm)
      }
    }
  }
</script>



