import Vue from 'vue'
import NProgress from 'nprogress'
import Router from 'vue-router'
import store from '@/js/vuex/store'
Vue.use(Router);
const router = new Router({
  mode:'history',
  routes: [
    {name: 'index', path: '', redirect: '/login', hidden: true},//空地址重定向到登录页面
    {name: 'login', path: '/login', component: (resolve) => require(['@/pages/admin/module/login/login.vue'], resolve),},
    {name: '404', path: '/404', component: (resolve) => require(['@/pages/admin/sys/error/404.vue'], resolve),},
    {
      name: 'main', path: '/main', meta: {title: "主页"}, component: (resolve) => require(['@/pages/admin/sys/home/main.vue'], resolve),
      children: [
        {name: "home", path: '/home', meta: {title: "平台首页"},  component: (resolve) => require(['@/pages/admin/sys/home/home.vue'], resolve),},
        {name: 'notFound', path: '*',  redirect: '/',  hidden: true}/*页面不存在的情况下会跳到404页面*/
      ]
    },
    {name: 'front', path: '/front', component: (resolve) => require(['@/pages/front/author/author.vue'], resolve)},
    {name: 'index', path: '/front/index', component: (resolve) => require(['@/pages/front/index/home.vue'], resolve)},
    {name: 'login', path: '/front/login', component: (resolve) => require(['@/pages/front/service/personal/userLogin.vue'], resolve)},
    {name: 'message', path: '/front/message', component: (resolve) => require(['@/pages/front/service/message/message.vue'], resolve)},
    {name: 'categoryList', path: '/front/categoryList', component: (resolve) => require(['@/pages/front/service/category/categoryList.vue'], resolve)},
    {name: 'categoryDetails', path: '/front/categoryDetails', component: (resolve) => require(['@/pages/front/service/category/categoryDetails.vue'], resolve)}
  ]
});

//路由拦截守卫
router.beforeEach((to, from, next) => {
  console.log("to:",to);
  NProgress.start();
  document.title = "欢迎登录";
  if (to.path.indexOf('/front') < 0 && to.path != "/login") {
    document.title = store.getters.config.projectName;
    if (store.getters.routes.length == 0) {
      store.dispatch('commitUserRoutes').then(res => {
        let accessRoutes = res.list;
        let routers = router.options['routes'];
        routers.forEach(item => {
          if (item.name === 'main') {
            let userRoutes = accessRoutes.map(item => {
              return {
                name: item.name,
                path: item.path,
                meta: {title: item.title},
                component: (resolve) => require(["@/pages/" + item.component], resolve),
              }
            });
            item.children.push(...userRoutes);
            console.log("userRoutes",userRoutes)
            router.addRoutes(routers);//重新覆盖,避免叠加
            next({...to, replace: true}) // hack方法 确保addRoutes已完成
          }
        })
      }).catch(err => {
        console.log("err:",to)
        store.dispatch('commitLoginOut').then(() => {
          next({path: '/login'})
          NProgress.done()
        })
      })
    } else {//路由加载过放行
      next()
      NProgress.done()
    }
  } else {//非登录页面放行
    next()
    NProgress.done()
  }
});

router.afterEach(() => {
  NProgress.done()
})
export default router



