<!--【首页查询区域】：包含查询按钮-->
<template>
   <div class="search">
      <!--搜索按钮-->
     <div>
       <el-input class="search-btn" v-model="queryForm.title" @clear="clear" placeholder="请输入搜索内容" clearable/><el-button type="primary" class="el-icon-search" @click="search"> 搜 索</el-button>
     </div>
      <!--分类展示-->
     <div class="category">
       <el-link class="category-link" v-for="item in categoryList" @click="toPage('/front/categoryList?id='+item.id)" :key="item.id" type="primary">{{item.name}}</el-link>
     </div>
   </div>
</template>

<script>
  export default {
    name: "head-search",
    data() {
      return {
        queryForm: {//与home-body保持一致
          current: 1,
          size: 8,
          title: "",
          content: ""
        },
        categoryList:[],
      }
    },
    computed: {},
    mounted(){
      this.getCategoryList();
    },
    methods: {
      search(){
        this.$emit('search',this.queryForm)
      },
      clear(){
        this.$emit('search',this.queryForm)
      },
      //查询分类数据
      getCategoryList() {
        this.$request.doGet('/api/category/getList').then(res => {
          if (res.isOk) {
            this.categoryList = res.list;
          }
        })
      },
    }
  }
</script>
<style scoped>
  .search {
    margin: 70px auto 0;
    padding-top: 60px;
    width: 1200px;
    text-align: center;
    background: white;
    height: 150px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
  }

  .search-btn {
    width: 500px
  }

  .category {
    padding: 10px
  }

  .category-link {
    padding: 5px 20px
  }
</style>



