<!--【首页中间区域】：包含列表展示-->
<template>
  <div class="home-body">
    <!--轮播组件-->
    <sui-wheel class="wheel" :showNotice="true"></sui-wheel>
    <div>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>消息通知</span>
        </div>
        <sui-card v-if="dataList.length>0" :dataList=dataList pageName="categoryDetails"></sui-card>
        <el-empty v-else class="sui-empty" description="暂无数据"></el-empty>
      </el-card>
    </div>
    <div class="pagination" style="text-align: center">
      <el-pagination
        @current-change="handleCurrentChange"
        :page-size="queryForm.size"
        layout="total, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        total: 0, //记录数
        categoryDict: [],
        dataList: [],
        queryForm: {
          current: 1,
          size: 8,
          id: "",
          picture: "",
          title: "",
          categoryId: "",
          publishTime: "",
          content: ""
        }
      };
    },
    mounted() {
      //初始化数据
      this.getList();
      //初始化【所属分类】
      this.getCategoryDict();
    },
    computed: {
    },
    methods: {
      //点击搜索
      handleQuery() {
        this.queryForm.current = 1;
        this.getList(this.queryForm);
      },
      //查询列表数据
      getList(queryForm) {
        this.$request.doGet('/api/notice/getPage', queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        });
      },
      //【所属分类】
      getCategoryDict() {
        this.$request.doGet('/api/category/getList').then(res => {
          if (res.isOk) {
            this.categoryDict = res.list;
          }
        });
      },
      //重置查询
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //选择页码
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
    }
  };
</script>
<style scoped>
  .box-card {
    min-height: 800px
  }
  .wheel{
    padding: 10px 0
  }
  .home-body {
    width: 1200px;
    margin: 0 auto;
  }
</style>






