<template>
  <!--  <div class="home" style="background-image: url('static/images/home/home.jpg');"></div>-->
  <div style="background: white;padding-right:20px">
    <el-upload
      :disabled="this.disabled"
      class="avatar-uploader"
      action="/admin/sysAttach/uploadFile/image/editor"
      :show-file-list="false"
      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload">
    </el-upload>
    <quill-editor
       :style="'height:'+height+'px'"
       v-model="content"
       @focus="onEditorFocus($event)"
      :options="editorOption"
      ref="QuillEditor">
    </quill-editor>
  </div>
</template>
<script>
  // 工具栏配置
  const toolbarOptions = [
    ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
    ['blockquote', 'code-block'],

    [{'header': 1}, {'header': 2}],               // custom button values
    [{'list': 'ordered'}, {'list': 'bullet'}],
    [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
    [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
    [{'direction': 'rtl'}],                         // text direction

    [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
    [{'header': [1, 2, 3, 4, 5, 6, false]}],

    [{'color': []}],
    [{'background': []}],          // dropdown with defaults from theme
    [{'font': []}],
    [{'align': []}],
    ['link', 'image', 'video'],
    ['clean'],
  ]
  // 标题
  const titleConfig = {
    'ql-bold':'加粗',
    'ql-color':'颜色',
    'ql-font':'字体',
    'ql-code':'插入代码',
    'ql-italic':'斜体',
    'ql-link':'添加链接',
    'ql-background':'背景颜色',
    'ql-size':'字体大小',
    'ql-strike':'删除线',
    'ql-script':'上标/下标',
    'ql-underline':'下划线',
    'ql-blockquote':'引用',
    'ql-header':'标题',
    'ql-indent':'缩进',
    'ql-list':'列表',
    'ql-align':'文本对齐',
    'ql-direction':'文本方向',
    'ql-code-block':'代码块',
    'ql-formula':'公式',
    'ql-image':'图片',
    'ql-video':'视频',
    'ql-clean':'清除字体样式',
    'ql-upload':'文件'
  };
  export default {
    model:{
      prop:'content',
      event:'contentChange'
    },
    props: {
      height: {
        type: Number,
        default:300
      },
      content: {
        type: String,
        default: ''
      },
      placeholder: {
        type: String,
        default: '请输入内容......'
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        aaa:true,
        editorOption: {
          placeholder: this.placeholder,
          readOnly: this.disabled,
          modules: {
            toolbar: {
              container: toolbarOptions,  // 工具栏
              handlers: {
                'image': function (value) {
                  if (value) {
                    // 调用iview图片上传
                    document.querySelector('.el-upload__input').click()
                  } else {
                    this.quill.format('image', false);
                  }
                }
              }
            }
          }
        }
      }
    },
    mounted () {
      this.addQuillTitle()
    },
    watch: {
      content(content) {
        this.$emit('contentChange',content);
      }
    },
    computed:{
    },
    methods: {
      onEditorFocus(event) {
        document.querySelector('.el-upload__input').disabled=this.disabled
        event.enable(!this.disabled);
      }, // 获得焦点事件
      addQuillTitle () {
        const oToolBar = document.querySelector('.ql-toolbar'),
          aButton = oToolBar.querySelectorAll('button'),
          aSelect =  oToolBar.querySelectorAll('select');
        aButton.forEach(function(item){
          if(item.className === 'ql-script'){
            item.value === 'sub' ? item.title = '下标': item.title = '上标';
          }else if(item.className === 'ql-indent'){
            item.value === '+1' ? item.title ='向右缩进': item.title ='向左缩进';
          }else{
            item.title = titleConfig[item.classList[0]];
          }
        });
        aSelect.forEach(function(item){
          item.parentNode.title = titleConfig[item.classList[0]];
        });
      },
      handleAvatarSuccess(res, file) {
        if (res.isOk) {
          let quill = this.$refs.QuillEditor.quill
          // 获取光标所在位置
          let length = quill.getSelection().index;
          // 插入图片，res为服务器返回的图片链接地址
          quill.insertEmbed(length, 'image', this.formatUrl(res.obj.savePath))
          // 调整光标到最后
          quill.setSelection(length + 1)
        } else {
          // 提示信息，需引入Message
          this.showWarn("图片上传失败")
        }
      },
      beforeAvatarUpload(file) {
        if(this.type==''){
          console.error("请设置附件类型")
        }
        const isJPG = file.type === 'image/jpeg';
        const isPng = file.type === 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!(isJPG||isPng)) {
          this.$message.error('上传头像图片只能是 jpg或者png 格式!');
          return false
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
          return false
        }
        return true;
      }
    }
  }
</script>

<style scoped>
</style>



