<!--【首页头部区域】：包含登录|注册-->
<template>
  <div class="body">
    <user-login ref="userLoginDialog"></user-login>
    <user-register ref="userRegisterDialog"></user-register>
    <user-info v-if="hasLogin" ref="userInfoDialog"/>
    <user-password v-if="hasLogin" ref="passwordDialog"/>
    <div class="header">
     <div class="header-content">
       <router-link :to="{ path: '/front' }" class="float-l pointer">
         <el-image class="header-logo" src="/static/images/front/logo/logo.png"></el-image>
         <span class="projectName">{{projectName}}</span>
       </router-link>
       <div class="header-banner">
         <router-link class="tab pointer" :to="{ path: '/front' }">首页</router-link>
         <router-link class="tab pointer" :to="{ path: '/front' }">个人中心</router-link>
         <router-link class="tab pointer" :to="{ path: '/front/message' }">留言板</router-link>
         <div v-if="hasLogin">
           <el-dropdown class="pointer float-r">
             <span class="el-dropdown-link">
               <el-avatar size="small" class="middle" :src="formatUrl(userInfo.photo)"></el-avatar>
               <span class="loginName">{{userInfo.loginName}}</span>
               <i class="el-icon-arrow-down el-icon--right"></i>
             </span>
             <el-dropdown-menu slot="dropdown">
               <el-dropdown-item icon="el-icon-user" @click.native="updateUserInfo">用户信息</el-dropdown-item>
               <el-dropdown-item icon="el-icon-key" @click.native="updatePassword">修改密码</el-dropdown-item>
               <el-dropdown-item divided icon="el-icon-switch-button" @click.native="loginOut">退出登录</el-dropdown-item>
             </el-dropdown-menu>
           </el-dropdown>
         </div>
         <div v-else class="float-r">
           <span class="tab" @click="userLogin">登 录</span>
           <span class="tab" @click="userRegister">注 册</span>
         </div>
       </div>
     </div>
    </div>
  </div>
</template>
<script>
  import {mapActions} from "vuex";
  export default {
    computed:{
      userInfo(){
        return this.$store.getters.userInfo;
      },
      hasLogin(){
        return this.$store.getters.hasLogin;
      },
      projectName(){
        return this.$store.getters.config.projectName;
      }
    },
    methods: {
      ...mapActions(['commitFrontLoginInfo']),
      loginOut(){
        const that = this;
        that.showConfirm('确认退出吗?',function (){
          that.$request.doGet("/api/login/loginOut").then(res=>{
            if(res.isOk){
              that.commitFrontLoginInfo();//更新登录信息
              that.$router.push("/front");
              that.showToast("退出成功");
            }
          })
        })
      },
      userLogin() {
        this.$refs.userLoginDialog.open({title:'账号登录'});
      },
      userRegister() {
        this.$refs.userRegisterDialog.open({title:'用户注册'});
      },
      updateUserInfo() {
        this.$refs.userInfoDialog.open({title:'用户信息'});
      },
      updatePassword() {
        this.$refs.passwordDialog.open({title:'修改密码'});
      },
    }
  }
</script>
<style scoped>
  .projectName {
    width: 200px;
    vertical-align: middle;
    font-size: 25px;
    font-weight: bold;
    font-family: cursive;
  }

  .el-dropdown-link {
    color: white;
    font-size: 16px;
  }

  .el-icon-arrow-down {
    font-size: 16px;
  }

  .tab {
    font-size: 16px;
    float: left;
    margin-left: 30px;
  }

  .tab:hover {
    color: #1c84c6;
  }

  .loginName {
    color: white;
    font-weight: bold;
    font-size: medium
  }

  .middle {
    vertical-align: middle
  }

  .header {
    height: 60px;
    z-index: 1000;
    background: #2b4a78;
    color: white;
    position: fixed;
    width: 100%
  }

  .header-content {
    padding: 5px;
    width: 1200px;
    margin: 0 auto;
  }

  .header-logo {
    vertical-align: middle;
    width: 50px;
    height: 50px;
  }

  .header-banner {
    line-height: 50px
  }
</style>




