<!--授权页面-->
<script>
  import {mapActions} from "vuex";
  import axios from "axios";

  export default {
    name: "author",
    computed: {},
    mounted() {
      this.getToken();
    },
    methods: {
      ...mapActions(['commitConfig', 'commitDictList', 'commitToken']),
      initData() {
        this.commitDictList();
        this.commitConfig();
        this.$router.push("/front/index")
      },
      //获取token
      getToken() {
        this.commitToken({from: 3}).then(res => {
          this.initData()
        })
      }
    },
  }
</script>



