<!--【首页底部区域】：包含底部说明-->
<template>
    <div class="footer">
      <div class="footer-content">
        <div class="tab-link">
          <router-link class="tab pointer" :to="{ path: '/front' }">首页 <span class="sept">|</span></router-link>
          <router-link class="tab pointer" :to="{ path: '/front' }">消息通知<span class="sept">|</span></router-link>
          <router-link class="tab pointer" :to="{ path: '/front' }">个人中心<span class="sept">|</span></router-link>
          <router-link class="tab pointer" :to="{ path: '/front' }">留言板<span class="sept">|</span></router-link>
          <router-link class="tab pointer" :to="{ path: '/front' }">联系我们<span class="sept">|</span></router-link>
        </div>
        <div class="clearfix"></div>
        <div class="pointer contact">
          <span class="el-icon-phone-outline phone">400-123456</span>
          <el-image class="official" src="/static/images/front/contact/wx_official.jpg"></el-image>
        </div>
      </div>
      <div class="copyright pointer">Copyright © 2022 by {{projectName}}</div>
    </div>
</template>

<script>
  export default {
    name: "footer",
    computed: {
      projectName() {
        return this.$store.getters.config.projectName
      },
    },
  }
</script>
<style scoped>
  .footer {
    background: #2b4a78;
    color: gainsboro;
  }

  .footer-content {
    margin: 0 auto;
    width: 1200px;
  }

  .contact {
    float: right;
    margin-right: 100px;
  }
  .sept {
    color: grey;
    padding-left: 15px
  }

  .copyright {
    clear: both;
    padding-bottom: 10px;
    text-align: center;
    font-size: smaller;
    color: #cccccc;
  }

  .tab {
    padding: 10px;
    font-size: 16px;
    float: left;
    margin-left: 30px;
  }

  .tab:hover {
    color: #1c84c6;
  }

  .tab-link {
    width: 800px;
    margin: 0 auto;
    padding: 10px
  }

  .phone {
    margin-right: 50px;
    font-size: xx-large;
  }

  .official {
    vertical-align: middle;
    width: 60px;
    height: 60px
  }

</style>



