<!-- 【分类详情】页面 -->
<template>
  <div class="front-page">
    <home-header></home-header>
    <div class="front-body">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/front' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>查看详情</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div content="article">
          <h1 class="font-shadow article-title">{{item.title}}</h1>
          <span class="article-time">{{item.createDate.split(' ')[0]}}</span>
          <el-image class="article-image" :src="formatUrl(item.picture)"></el-image>
          <div v-html="item.content"></div>
        </div>
      </el-card>
    </div>
    <home-footer></home-footer>
  </div>

</template>

<script>
  export default {
    name: "itemForm",
    data() {
      return {
        //表单数据
        item: {
          id: "",
          picture: "",
          title: "",
          type: "",
          publishTime: "",
          content: ""
        },
      }
    },
    mounted(){
      let params = this.$route.query;
      this.getItem(params.id);
    },
    methods: {
      //根据id获取数据
      getItem(id) {
        this.$request.doGet("/api/notice/get?id=" + id).then(res => {
          if (res.isOk) {
            this.item = res.obj;
          }
        });
      },
    }
  }
</script>
<style scoped>
  .box-card {
    padding-bottom: 20px;
    min-height: 1000px
  }

  .article-title {
    font-size: 22px;
    margin-top: 20px;
    text-align: center
  }

  .article-time {
    font-size: 22px;
    display: block;
    font-size: small;
    margin-top: 20px;
    text-align: center;
    color: #999
  }

  .article-image {
    width: 300px;
  }
</style>



