<template>
<!-- 要 picture、name(或者title)、id-->
  <div>
    <div v-for="item in dataList" :key="item.id" style="width: 25%;float: left;padding: 5px;">
      <el-card shadow="hover" class="pointer">
        <div style="height: 250px">
          <el-image style="height: 150px;width: 100%" :src="formatUrl(item.picture)"></el-image>
          <b class="font-overflow" style="display: block;padding: 10px 0;font-family: cursive">{{item.title==null?item.name:item.title}}</b>
          <div v-html="delHtmlTag(item.content)" class="content"></div>
        </div>
        <div style="padding: 15px">
          <span style="float: left; font-size: 14px;color: #999;margin-top: 5px;">{{item.createDate.split(' ')[0]}}</span>
          <el-button @click="toPage({name:pageName,query:{id:item.id}})" type="text" style="float: right;margin-bottom: 5px" size="mini">查看详情</el-button>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
    export default {
      props: {
        dataList: {
          type: Array,
          default: []
        },
        pageName: {
          type: String,
          default: ""
        },
      },
      methods: {
        delHtmlTag(str){
          return str.replace(/<[^>]+>/g,"");  //正则去掉所有的html标记
        },
      }
    };
</script>
<style scoped>
  .content {
    font-size: 14px;
    color: #999;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }
</style>



