<!--折线图-->
<template>
  <div id="bendChart" :style="style"></div>
</template>
<script>
  export default {
    name: 'echarts-bend',
    props: {
      style: {
        type: Object,
        default: {width: '100%', height: '500px'}
      },
      title: {
        type: String,
        default: "折线图"
      },
      xData: {//['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
        type: Array,
        default: []
      },
      yData: {//[150, 230, 224, 218, 135, 147, 260]
        type: Array,
        default: []
      },
    },
    data() {
      return {}
    },
    mounted() {
      this.drawBend(this.xData, this.yData);
    },
    methods: {
      drawBend(xData, yData) {
        // 基于准备好的dom，初始化echarts实例
        let bendChart = this.$echarts.init(document.getElementById('bendChart'))
        // 绘制图表
        let option = {
          title: {text: this.title, left: 'center'},
          xAxis: {
            type: 'category',
            data: xData
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              data: yData,
              type: 'line',
              smooth: true,
              itemStyle: {        //上方显示数值
                normal: {
                  label: {
                    show: true, //开启显示
                    position: 'top', //在上方显示
                    textStyle: { //数值样式
                      color: 'gray',
                      fontSize: 16
                    }
                  }
                }
              }
            }
          ]
        };
        bendChart.setOption(option);
      }
    }
  }
</script>



